import { useSelector,useDispatch } from "react-redux"
import { useState } from "react";
import {createGallerias } from "../../reducers/galleriaReducer";
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import {notice} from "../../reducers/notificationReducer";
import { Link } from "react-router-dom";


function GalleriaBack(){
const data = useSelector(state => state.galleria)
const user = useSelector(state => state.currentUser)
const dispatch = useDispatch()
    const [text,setText] = useState("");
    const [img,setImg] = useState("");


const addGalleria = (event) => {
    event.preventDefault()
    const newGalleria = ({
        text: text,
        img: img
    })
    dispatch(createGallerias(newGalleria))
    setText("")
    setImg("")
    dispatch(notice("lisäsit uuden kuvan",3));
}

const handleTextChange = (event) => {
    console.log(event.target.value)
    setText(event.target.value)
  }
  const handleImgChange = (event) => {
    console.log(event.target.value)
    setImg(event.target.value)
  }

    const gallery = data.map(x =>{
        return(
            <div key={x.id}>
                <h2 style={{margin:"5px",color:"white"}}>{x.text}</h2>
          
               <Link to={`/control/galleria/${x.id}`} > <img className="galleria-img" src={x.img} alt={x.id}></img></Link>

            </div>
        )
    })
if(user){
    return(
        <div className="etusivu">
<Form onSubmit={addGalleria}>
<Form.Group>
<Form.Control id="text"
            value={text}
            onChange={handleTextChange}
            type="text"
            placeholder="tekstiä"></Form.Control>
          <Form.Control  id="img"
            value={img}
            onChange={handleImgChange}
            type="text"
            placeholder="kuva linkki"></Form.Control>
            <Button id="addGalleria" type="submit">
            lisää
          </Button>
</Form.Group>
</Form>
       {gallery}
    </div>
    )

}
else
{
    return(
    <>
    <h2 style={{color:"white"}}>Kirjaudu sisään ensin</h2>
    </>
    )
}
}
export default GalleriaBack